import { AddCircle, Mic, Send } from '@mui/icons-material';
import { Box, Button, IconButton, Paper, TextField, Typography, useMediaQuery, useTheme } from '@mui/material';
import { styled } from '@mui/system';
import React, { useRef, useState } from 'react';
import { speechToText } from '../api';
import { useNavigate } from 'react-router-dom';


const PageContainer = styled('div')({
  padding: '30px',
  display: 'flex',
  flexDirection: 'column',
  minHeight: '100vh',
  fontSize: '1.2rem',
});

const TopicDisplayContainer = styled(Paper)(({ theme }) => ({
  position: 'fixed',
  top: '30%',
  left: '45%', // 30%
  transform: 'translate(-50%, -50%)',
  width: '48%', // ActionContainer
  height: '150px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  fontSize: '2.5rem',
  fontWeight: 'bold',
  textAlign: 'center',
  backgroundColor: '#f5f5f5',
  [theme.breakpoints.up('md')]: {
    left: '45%',
    width: '48%',
  },
}));

const ActionContainer = styled(Box)(({ theme }) => ({
  position: 'fixed',
  top: '30%', // TopicDisplayContainer
  right: '14%', // 10%
  transform: 'translateY(-50%)', // 
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
  padding: '10px',
  [theme.breakpoints.up('md')]: {
    left: 'auto',
    right: '14%',
    transform: 'translateY(-50%)',
  },
}));

const ImageDisplayContainer = styled(Box)({
  display: 'flex',
  flexWrap: 'wrap',
  justifyContent: 'center',
  padding: '20px',
  position: 'fixed',
  top: '45%',  // 
  left: '50%',
  transform: 'translateX(-50%)',
  width: '80%',
  maxHeight: '30vh',
  overflowY: 'auto'
});

const ConfirmButtonContainer = styled(Box)({
  position: 'fixed',
  bottom: '90px', //  VoiceInputContainer 
  left: '50%',
  transform: 'translateX(-50%)',
  width: '80%',
  display: 'flex',
  justifyContent: 'center',
  padding: '20px',
});

const VoiceInputContainer = styled(Box)({
  position: 'fixed',
  bottom: '0',
  left: '0',
  right: '0',
  display: 'flex',
  alignItems: 'center',
  padding: '20px',
  backgroundColor: '#fff',
});

function NewEntryPage() {
  const [topic, setTopic] = useState('');
  const [currentInput, setCurrentInput] = useState('');
  const [isRecording, setIsRecording] = useState(false);
  const mediaRecorder = useRef(null);
  const audioChunks = useRef([]);
  const [uploadedImages, setUploadedImages] = useState([]);
  const navigate = useNavigate();

  const handleImageUpload = () => {
    const presetImages = [
      '/1.jpg',
      '/2.jpg',
      '/3.jpg',
      '/4.jpg'
    ];
    setUploadedImages(presetImages);
  };
  
  const handleConfirm = () => {
    // 
    //  EntryDetails 
    navigate('/entry-details', { state: { images: uploadedImages, topic: topic } });
  };
  
  const hasUploadedImages = uploadedImages.length > 0;


  const startRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      mediaRecorder.current = new MediaRecorder(stream);

      mediaRecorder.current.ondataavailable = (event) => {
        audioChunks.current.push(event.data);
      };

      mediaRecorder.current.onstop = async () => {
        const audioBlob = new Blob(audioChunks.current, { type: 'audio/wav' });
        audioChunks.current = [];
        const text = await speechToText(audioBlob);
        setCurrentInput(text);
      };

      mediaRecorder.current.start();
      setIsRecording(true);
    } catch (error) {
      console.error('Error starting recording:', error);
    }
  };

  const stopRecording = () => {
    if (mediaRecorder.current) {
      mediaRecorder.current.stop();
      setIsRecording(false);
    }
  };

  const handleSend = () => {
    if (currentInput.trim()) {
      setTopic(currentInput.trim());
      setCurrentInput('');
    }
  };


  const theme = useTheme();
  const isMdUp = useMediaQuery(theme.breakpoints.up('md'));


  return (
    <PageContainer>
      <Typography variant="h3" style={{ textAlign: 'center', marginBottom: '30px', fontWeight: 'bold' }}>
       Log New Diary
      </Typography>

      <TopicDisplayContainer elevation={3}>
        {topic ? topic : <Typography color="textSecondary" style={{ fontSize: 'inherit' }}>Your Topic</Typography>}
      </TopicDisplayContainer>

      <ActionContainer>
        <IconButton size="large" onClick={handleImageUpload}>
          <AddCircle style={{ fontSize: isMdUp ? '4rem' : '3rem' }} />
        </IconButton>
        <Typography variant={isMdUp ? "h6" : "body1"} style={{ marginTop: '10px' }}>
          Please upload photos
        </Typography>
      </ActionContainer>
      
      <ImageDisplayContainer>
      {uploadedImages.map((img, index) => (
        <img 
          key={index} 
          src={img} 
          alt={`Uploaded ${index + 1}`} 
          style={{ width: '200px', height: '150px', objectFit: 'cover', margin: '5px' }} 
        />
      ))}
    </ImageDisplayContainer>

    <ConfirmButtonContainer>
        <Button 
          variant="contained" 
          color={hasUploadedImages ? "primary" : "inherit"}
          size="large" 
          onClick={handleConfirm}
          disabled={!hasUploadedImages}
          sx={{ 
            fontSize: '1.5rem', 
            padding: '15px 50px',
            borderRadius: '30px',
            backgroundColor: hasUploadedImages ? undefined : '#9e9e9e',
            color: hasUploadedImages ? undefined : '#ffffff',
            '&:hover': {
              backgroundColor: hasUploadedImages ? undefined : '#9e9e9e',
            }
          }}
        >
          Confirm
        </Button>
      </ConfirmButtonContainer>

      <ImageDisplayContainer>
        {uploadedImages.map((img, index) => (
          <img 
            key={index} 
            src={img} 
            alt={`Uploaded ${index + 1}`} 
            style={{ width: '200px', height: '180px', objectFit: 'cover', margin: '5px' }} 
          />
        ))}
      </ImageDisplayContainer>

      <ConfirmButtonContainer>
        <Button 
          variant="contained" 
          disabled={!hasUploadedImages}
          onClick={handleConfirm}
          sx={{ 
            fontSize: '1.5rem', 
            padding: '15px 50px',
            borderRadius: '30px',
            backgroundColor: hasUploadedImages ? 'primary.main' : '#9e9e9e',
            color: '#ffffff',
            '&:hover': {
              backgroundColor: hasUploadedImages ? 'primary.dark' : '#9e9e9e',
            },
            '&.Mui-disabled': {
              backgroundColor: '#9e9e9e',
              color: '#ffffff',
            }
          }}
        >
          CONFIRM
        </Button>
      </ConfirmButtonContainer>

      <VoiceInputContainer>
        <TextField 
          value={currentInput} 
          onChange={(e) => setCurrentInput(e.target.value)}
          placeholder="Input your topic, e.g. Hawaii travel"
          fullWidth
          variant="outlined"
          InputProps={{
            style: { fontSize: '1.4rem' }
          }}
        />

        <IconButton onClick={isRecording ? stopRecording : startRecording} size="large">
          <Mic color={isRecording ? "secondary" : "primary"} style={{ fontSize: '2rem' }} />
        </IconButton>
        <IconButton onClick={handleSend} size="large">
          <Send style={{ fontSize: '2rem' }} />
        </IconButton>
      </VoiceInputContainer>
    </PageContainer>
  );
}

export default NewEntryPage;

